import axios from "axios"
import { EVENT_SELECTED, FETCH_EVENTS, CREATE_SUPER_ADMIN } from "./types"
import jwt from "jsonwebtoken"

axios.defaults.baseURL = process.env.REACT_APP_API_URL


// Action Creator

export const selectEvent = event => {
    // return an action
    return {
        type: EVENT_SELECTED,
        payload: event
    }
}


export const fetchEvents = () => async (dispatch) => {

    const response = await axios.get("/get_all_events", {
        headers: {
            'Access-Control-Allow-Origin': '*',
        },
    })

    dispatch({ type: FETCH_EVENTS, payload: response.data.events })
}

export const superAdminCreation = (username, password, secret) => async (dispatch) => {

    const data = {
        username, password, secret
    }
    const response = await axios.post(
        "/create_super_admin",
        data,
        {
            headers: {
                'Access-Control-Allow-Origin': '*',
            },
        })

    const auth_obj = _token_extraction(response.data.token)

    dispatch({ type: CREATE_SUPER_ADMIN, payload: auth_obj })
}


const _token_extraction = (token) => {
    let authObject = {
        token: "",
        userID: null,
        username: "",
        accessType: null,
        isAthenticated: false
    }

    try {
        if (token) {

            const decoded = jwt.decode(token)
            const {
                userID,
                username,
                accessType,
                exp: tokenExpiration
            } = decoded

            authObject.userID = userID
            authObject.username = username
            authObject.accessType = accessType
            authObject.token = token
            authObject.isAuthenticated = true

            localStorage.setItem("token", token)
            localStorage.setItem("userID", userID)
            localStorage.setItem("username", username)
            localStorage.setItem("accessType", accessType)
            localStorage.setItem("isAuthenticated", true)
            localStorage.setItem("tokenExpiration", tokenExpiration)

            return authObject

        }
    } catch (ex) {
        return ("Invalid Credentials")
    }
}