import React, { Component, Fragment } from "react"
import { connect } from "react-redux"

import Back from "../common/Back"
import Selector from "../common/Selector"
import { selectEvent, fetchEvents } from "../../actions"

import back_arrow from "../../static/images/back_arrow.svg"
import location from "../../static/images/location.svg"


import "./events.scss"

class Events extends Component {

    state = {
        selected: null,
        trans_x: 0
    }

    componentDidMount() {
        this.props.fetchEvents()
    }

    do_select = (option, index) => {
        this.setState({ selected: index })
    }

    select_this_event = (event) => {
        this.props.selectEvent(event)
    }

    go_left = () => {
        let { trans_x } = this.state

        if (window.innerWidth < 480) {
            if (trans_x < 0) {
                trans_x += 350
                this.setState({ trans_x })
            }

        } else {
            if (trans_x < 0) {
                trans_x += 400
                this.setState({ trans_x })
            }
        }


    }

    go_right = () => {
        let { trans_x } = this.state
        let { events } = this.props

        if (window.innerWidth < 480) {
            if (trans_x > -(events.length) * 350) {
                trans_x -= 350
                this.setState({ trans_x })
            }

        } else {
            if (trans_x > -(events.length) * 400) {
                trans_x -= 400
                this.setState({ trans_x })
            }
        }


    }

    render() {
        const { selected, trans_x } = this.state
        let { event_type, events } = this.props

        return (
            <Fragment>
                <Back route="/" />

                <div className="event">

                    <div className="event_head">
                        <div className="event_title">

                            <p className="event_title_t">RECENT EVENTS</p>
                            <p className="event_title_desc">Many desktop publishing packages and web page editors now</p>

                        </div>

                        <div className="event_settings">

                            <div className="event_settings_type">

                                <div className="event_settings_type_controls">
                                    <Selector title="EVENT TYPES" do_select={this.do_select} data={event_type} selected={selected} />
                                </div>
                            </div>

                            <div className="event_settings_navigate">
                                <div className="event_settings_navigate_actions">
                                    <img onClick={this.go_left} src={back_arrow} alt="backward" />
                                    <img onClick={this.go_right} className="event_settings_navigate_actions_forward" src={back_arrow} alt="forward" />
                                </div>
                            </div>

                        </div>
                    </div>

                    <div className="event_body">
                        <div className="event_body_elements">

                            {events.length === 0 ? <div>No Events</div> : events.map((event, index) => (
                                <div onClick={() => this.select_this_event(event)} key={index} style={{ transform: `translateX(${trans_x}px)` }} className="event_body_elements_el">
                                    <div className="event_body_elements_el_details" style={event.type !== "Upcoming Event" ? { backgroundColor: "rgba(255, 255, 255, .07)" } : {}}>
                                        <div className="event_body_elements_el_details_header">
                                            <div className="event_body_elements_el_details_header_upcoming">{event.type === "Upcoming Event" ? 'UPCOMING EVENT' : ''}</div>
                                            <div className="event_body_elements_el_details_header_date">
                                                <h3>{event.date}<span>{event.month}</span></h3>
                                                <h2>{event.title}</h2>
                                            </div>
                                        </div>
                                        <div className="event_body_elements_el_details_body">
                                            <p className="event_body_elements_el_details_body_desc">{event.description}</p>
                                        </div>
                                        <div className="event_body_elements_el_details_footer">
                                            <div className="event_body_elements_el_details_footer_icon">
                                                <img src={location} alt="location" />
                                            </div>
                                            <p>{event.location}</p>
                                        </div>
                                    </div>
                                    <img className="event_body_elements_el_img" src="/images/planet-1.png" alt="planet" />
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            </Fragment>
        )
    }
}

const mapStateToProps = (state) => {
    return { events: state.events }
}

export default connect(mapStateToProps, {
    selectEvent, fetchEvents
})(Events)