import React, { Component } from 'react'
import jwt from "jsonwebtoken"
import Back from "../common/Back"
import axios from "axios"

import "./adminPanel.scss"

axios.defaults.baseURL = process.env.REACT_APP_API_URL

class AdminPanel extends Component {

    state = {
        account: { username: "", password: "" },
        isLogin: true
    }

    get_request = async (url) => {
        const response = await axios.get(url, {
            headers: {
                'Access-Control-Allow-Origin': '*',
            },
        })
        return response
    }
    post_request = async (url, data) => {
        const response = await axios.post(
            url,
            data, {
            headers: {
                'Access-Control-Allow-Origin': '*',
            },
        })
        return response
    }

    handleChange = ({ currentTarget: input }) => {
        const account = { ...this.state.account }
        account[input.name] = input.value
        this.setState({ account })
    }


    handle_submit = async (e) => {
        e.preventDefault()
        console.log("prevented")
        const account = { ...this.state.account }
        let username = account.username
        let password = account.password

        let response = await this.post_request("/admin_login", { username, password })
        console.log(response.data)

        try {
            const { token } = response.data
            if (token) {
                localStorage.setItem("token", token)
                const decoded = jwt.decode(token)
                const {
                    adminID: userID,
                    name: username,
                    accessType,
                    exp: tokenExpiration
                } = decoded
                localStorage.setItem("userID", userID)
                localStorage.setItem("username", username)
                localStorage.setItem("accessType", accessType)
                localStorage.setItem("tokenExpiration", tokenExpiration)

                this.props.history.push("/api/admin/myTeachers")
            }
        } catch (ex) {
            console.log("Invalid Credentials")
        }
    }


    render() {
        return (
            <div>
                <Back route="/" />
                <div className="admin_login_box">
                    <h2>Login</h2>
                    <form onSubmit={this.handle_submit}>
                        <div className="inputBox">
                            <input onChange={this.handleChange} type="text" name="username" value={this.state.account.username} />
                            <label htmlFor="">Username</label>
                        </div>
                        <div className="inputBox">
                            <input onChange={this.handleChange} type="password" name="password" value={this.state.account.password} />
                            <label htmlFor="">Password</label>
                        </div>
                        <input type="submit" name="" value="Submit" />
                    </form>
                </div>
            </div>
        )
    }
}

export default AdminPanel
