import React, { Component } from 'react'
import { Redirect } from "react-router-dom"
import { connect } from "react-redux"
import { superAdminCreation } from "../../actions"


import Back from "../common/Back"
import axios from "axios"

import "./adminPanel.scss"

axios.defaults.baseURL = process.env.REACT_APP_API_URL


class CreateSuperAdmin extends Component {
    state = {
        account: { username: "", password: "", secret: "" },
        isLogin: false
    }

    handleChange = ({ currentTarget: input }) => {
        const account = { ...this.state.account }
        account[input.name] = input.value
        this.setState({ account })
    }


    handle_submit = async (e) => {
        e.preventDefault()
        const account = { ...this.state.account }
        this.props.superAdminCreation(account.username, account.password, account.secret)
    }


    render() {
        const { auth_data } = this.props

        return (
            <div>
                <Back route="/" />
                {(auth_data.token && auth_data.isAuthenticated === true) ? <Redirect to="/super_admin" /> : null}
                <div className="admin_login_box">
                    <h2>Login</h2>
                    <form onSubmit={this.handle_submit}>
                        <div className="inputBox">
                            <input onChange={this.handleChange} type="text" name="username" value={this.state.account.username} />
                            <label htmlFor="">Username</label>
                        </div>
                        <div className="inputBox">
                            <input onChange={this.handleChange} type="password" name="password" value={this.state.account.password} />
                            <label htmlFor="">Password</label>
                        </div>
                        <div className="inputBox">
                            <input onChange={this.handleChange} type="password" name="secret" value={this.state.account.secret} />
                            <label htmlFor="">Secret</label>
                        </div>
                        <input type="submit" name="" value="Submit" />
                    </form>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return { "auth_data": state.auth }
}

export default connect(mapStateToProps, {
    superAdminCreation
})(CreateSuperAdmin)





