import { GET_ERROR, CLEAR_ERROR } from "../actions/types"
const initial_state = {
    msg: "",
    status: null,
    id: null
}

export const errorReducer = (state = initial_state, action) => {
    switch (action.type) {
        case GET_ERROR:
            return {
                msg: action.payload.msg,
                status: action.payload.status,
                id: action.payload.id
            }
        case CLEAR_ERROR:
            return {
                msg: "",
                status: null,
                id: null
            }
        default:
            return state
    }
}