import { FETCH_EVENTS, EVENT_SELECTED } from "../actions/types"


export const eventsReducer = (state = [], action) => {
    switch (action.type) {
        case FETCH_EVENTS:
            return action.payload
        default:
            return state
    }

}

export const selectedEventReducer = (selectedEvent = null, action) => {
    if (action.type === EVENT_SELECTED) {
        return action.payload
    }

    return selectedEvent
}

