import React, { Component, Fragment } from "react"

import Back from "../common/Back"

import "./contact.scss"

class Contacts extends Component {
    render() {
        return (
            <Fragment>
                <Back route="/" />
                <div className="contact">
                    <div className="contact_back">

                    </div>
                    <div className="contact_all">
                        <h2>CONTACT US</h2>
                    </div>
                </div>
            </Fragment>
        )
    }
}

export default Contacts